import {getAddressList} from '@/http/product.js'
import loading from '@/components/loading2.vue'
export default {
	components: {
		loading
	},
	data(){
		return {
			beloading:false,
			id:'',
			pages:1,
			params:{
				"pageNum": 1,
				"pageSize": 10
			},
			checkObj:'',
			ifSelect:false,
			addressList:[
				/* {
					name:'小米',
					id:'1',
					ifDefault: true,
					area:'广东省|广州市|天河区',
					address:'天河公园',
					checked: true
				},
				{
					name:'克里斯',
					id:'2',
					ifDefault: false,
					area:'广东省|广州市|天河区',
					address:'天河公园心园新村北师大街1巷6号',
					checked: false
				} */
			]
		}
	},
	onShow(){
		this.init();
	},
	onLoad(params){
		
		//this.init();
	},
	onReachBottom(){
		//加载下一页
		this.params.pageNum += 1;
		if(this.pages>=this.params.pageNum){
			this.getAddressListFun();
		}
		
	},
	methods:{
		init(){
			if(this.$root.$mp.query.ifSelect){
				this.ifSelect=true;
			}else{
				this.ifSelect=false;
			}
			if(this.$root.$mp.query.id){
				this.id = this.$root.$mp.query.id;
			}
			this.checkObj = '';
			this.getAddressListFun();
		},
		getAddressListFun(){
			this.beloading = true;
			//this.addressList=[];
			if(this.params.pageNum===1){
				this.addressList = [];
			}
			getAddressList(this.params).then(data=>{
				this.beloading = false;
				if(data.data.retCode===0){
					this.pages = data.data.result.pages
					let addressList = data.data.result.list.map(item=>{
						item.checked = false;
						return item;
					});
					this.addressList = [...this.addressList,...addressList]
				}
			}).catch(e=>{
				this.beloading = false;
			})
		},
		submit(){
			if(!this.checkObj){
				uni.showToast({
					title: '请选择地址',
					duration: 2000,
					icon:'none'
				});
			}
			let obj = {
				userName: this.checkObj.receiver,
				userPhone: this.checkObj.phone,
				userAddress: this.checkObj.province + this.checkObj.city + this.checkObj.districts + this.checkObj.address
			}
			uni.navigateTo({
				url: '/pages/product/order?id='+ this.id +'&address='+JSON.stringify(obj)+'&shopId='+this.$store.state.shopId
			});
		},
		deleteFun(id){
			uni.showModal({
				title: '确认框',
				content: '是否确认删除？',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		editFun(id){
			let url = '/pages/my/editAddress?id='+id+'&shopId='+this.$store.state.shopId;
			if(this.ifSelect){
				url = url+'&ifSelect=1'
				if(this.id){
					url = url+'&productId='+this.id;
				}
			}else if(this.id){
				url = url+'&productId='+this.id;
			}
			uni.navigateTo({
				url: url
			});
		},
		addAddress(){
			let url = '/pages/my/editAddress'+'?shopId='+this.$store.state.shopId;
			if(this.ifSelect){
				url = url+'&ifSelect=1'
				if(this.id){
					url = url+'&productId='+this.id;
				}
			}else if(this.id){
				url = url+'&productId='+this.id;
			}
			uni.navigateTo({
				url: url
			});
			
		},
		changeCheck(item){
			this.checkObj = item;
			this.addressList.forEach(it=>{
				if(it.id === item.id){
					it.checked = true;
				}else{
					it.checked = false;
				}
			})
		}
	}
}
